import { useMemo } from 'react';
import { useParams } from 'react-router';

import { isMatchedBy } from '@mssgme/shared';
import { useHomePageData, usePageData, useWebsiteData } from '../../hooks';

export const useCurrentPage = () => {
    const { pageSlug } = useParams();
    const website = useWebsiteData();
    const homePage = useHomePageData();
    const basePage = usePageData() || homePage;
    const page = useMemo(() => {
        if (pageSlug) {
            const found = website.pages.find(isMatchedBy(pageSlug, 'slug'));

            if (found) {
                return found;
            }
        }

        return basePage;
    }, [pageSlug, website, basePage]);

    return {
        website,
        homePage,
        page,
    };
};
